var __MOR_COMPONENT__ = require('@morjs/core').createComponent;

var __MOR_API__ = require('@morjs/api').mor;

import TabMenu from './data';
__MOR_COMPONENT__({
    data: {
        active: 0,
        list: TabMenu,
    },
    methods: {
        onChange(event) {
            this.setData({ active: event.detail.value });
            __MOR_API__.switchTab({
                url: this.data.list[event.detail.value].url.startsWith('/')
                    ? this.data.list[event.detail.value].url
                    : `/${this.data.list[event.detail.value].url}`,
            });
        },
        init() {
            const page = getCurrentPages().pop();
            const route = page ? page.route.split('?')[0] : '';
            const active = this.data.list.findIndex((item) => (item.url.startsWith('/') ? item.url.substr(1) : item.url) ===
                `${route}`);
            this.setData({ active });
        },
    },
}, "w");
